.product{
    border-radius: 5px;
    margin: 1%;
    font-size: 13px;
    width: 30%;
    padding: 10px 0px;
    color: black;
    font-family: inherit;
}

.back-tres{
    background-color: #00B18F;
}
.back-dos{
    background-color: #FFBE00;
}
.blocks{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 10px 0px 15px;
