input{
    border-radius: 5px;
    margin-bottom: 20px;
}

.head{
    border-bottom-style: double;
    font-weight: 1000;
}

.sign{
    font-size: 8px;
    color: white;
    height: 18px;
    width: 18px;
    background-color: #282c34;
    padding: 5px;
    border-radius: 3px;
    margin: 0 5px 0  5px;
    border-style: none;
}

i{
    margin: 0px;
}
.total-price{
    margin: 30px;
    font-weight: 1000;
    border-style: groove;
}

.prod{
    margin-top: 10px;
}
header{
  background-color: #282c34;
  min-height: 10vh;
  display: block;
  text-align: center;
}

.name-header{
  width: 95%;
  font-size: 40px;
  color: white;
  float: left;
}

.link-router{
  width: 5%;
  font-size: 1.4em;
  background-color: azure;
  border-radius: 5px;
  padding: 5px;
  -webkit-align-content: center;
          align-content: center;
}


.ped{
    border-style: groove;
    border-radius: 10px;
    width: 331px;
    text-align: -webkit-center;
    padding: 10px;
    margin: 5px;
    font-weight: 1000;
}
.pedidos-fire{
    margin: 10px 0;
    font-size: 18px;
}
.title{
    font-size: 20px;
